body.timer-style {
  text-align: center;
  background: #a4b2c1;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  gap: 24px;
  display: flex;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #569ff7;
  border: 1px solid #569ff7;
  border-radius: 32px;
  padding: 6px 22px;
  font-size: 20px;
}

button:hover, button:focus {
  color: #569ff7;
  background-color: #fff;
}

button[disabled] {
  color: #f7f3f3;
  background-color: #d2cce1;
  border-color: #8276ab;
}

#datetime-picker {
  text-align: center;
  color: #468ee4;
  border: 1px solid #569ff7;
  border-radius: 32px;
  margin-right: 20px;
  padding: 6px 22px;
  font-size: 20px;
}

#datetime-picker[disabled] {
  color: #c9c1e0;
  background-color: #ccc;
  border-color: #ccc !important;
}

.timer {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.field {
  color: #f4f3f8;
  min-width: 150px;
  border: 1px solid #e4e8ed;
  border-radius: 34px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 6px 22px;
  font-size: 20px;
  display: flex;
}

.field:not(:last-child) {
  margin-right: 20px;
}

.value {
  font-size: 30px;
  font-weight: 700;
}

